import { Component, Input, OnInit, inject } from '@angular/core';
import { Exercise, ExercisesService } from '@slowautomobile/exercises';
import { Observable, map } from 'rxjs';
import { AsyncPipe } from '@angular/common';
import { NgxPrintDirective } from 'ngx-print';
import { MatButton } from '@angular/material/button';

@Component({
  selector: 'sanareapp-print-template',
  templateUrl: './print-template.component.html',
  styleUrl: './print-template.component.scss',
  standalone: true,
  imports: [MatButton, NgxPrintDirective, AsyncPipe],
})
export class PrintTemplateComponent implements OnInit {
  private exercisesServices = inject(ExercisesService);

  exercises: Exercise[] = [];
  chosenExercises$!: Observable<Exercise[]>;
  @Input() data!: Exercise[];

  ngOnInit(): void {
    this.chosenExercises$ = this.exercisesServices.getExercises().pipe(
      map(exercises => {
        exercises.map((exercise, i) => {
          exercise.sets = 10;
          exercise.reps = 10;
          exercise.pause = '30s';
          i % 2 === 1 && i < 2
            ? (exercise.weight = '')
            : (exercise.weight = '5kg'),
            i % 2 === 0
              ? (exercise.equipments = [
                  {
                    name: 'Medicine balls',
                    translations: {
                      en: 'Medicine balls Loptanga',
                      de: 'Medcin De Ball',
                      sr: 'Loptanga',
                    },
                  },
                  // {
                  //   name: 'Something Eq',
                  //   translations: { en: 'Something Eq balls' },
                  // },
                ])
              : (exercise.equipments = []);
        });
        return exercises.slice(0, 6);
      }),
    );
    console.log(this.data);
  }
}
